.thank-section {
	position: relative;
	padding: 5rem 0 0;
	overflow: hidden;
	@include max-md {
		padding: 3rem 0 0;
	}
	.image-wrapper {
		position: relative;
		width: 90%;
		height: auto;
		aspect-ratio: 7/8;
		margin: 0 auto;
		.orn-1 {
			position: absolute;
			top: 0;
			left: 0;
			width: 200px;
			transform: translate(-35%, -15%) scaleX(-1);
		}
		.orn-2 {
			position: absolute;
			top: 0;
			right: 0;
			width: 200px;
			transform: translate(35%, -15%);
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 4px;
				border: 1rem solid var(--color-primary);
			}
		}
	}
	.thank-wrapper {
		position: relative;
		padding: 0 0 5rem;
		@include max-md {
			padding: 0 0 3rem;
		}
		.side-1 {
			position: absolute;
			top: 0;
			left: 0;
			width: auto;
			height: 100%;
			.image-element {
				width: auto;
				height: 100%;
				img {
					width: auto;
					height: 100%;
				}
			}
		}
		.side-2 {
			position: absolute;
			top: 0;
			right: 0;
			width: auto;
			height: 100%;
			transform: scaleX(-1);
			.image-element {
				width: auto;
				height: 100%;
				img {
					width: auto;
					height: 100%;
				}
			}
		}
		.ornaments-wrapper {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			z-index: 5;
			.orn-1 {
				position: absolute;
				bottom: 5rem;
				left: 0;
				width: 240px;
				transform: translate(-70%, 0);
				.orn-2 {
					position: absolute;
					bottom: 0;
					right: 0;
					width: 200px;
					transform: translate(25%, 40%);
					.orn-3 {
						position: absolute;
						bottom: 0;
						right: 0;
						width: 120px;
						transform: translate(-30%, 30%);
					}
				}
				.orn-4 {
					position: absolute;
					top: 0;
					right: 0;
					width: 120px;
					transform: translate(-5%, -10%);
					.orn-5 {
						position: absolute;
						top: 0;
						right: 0;
						width: 100px;
						transform: translate(-15%, -65%);
					}
				}
			}
			.orn-6 {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 240px;
				transform: translate(-15%, 40%);
			}
			.orn-7 {
				position: absolute;
				bottom: 5rem;
				right: 0;
				width: 240px;
				transform: translate(70%, 0) scaleX(-1);
				.orn-8 {
					position: absolute;
					bottom: 0;
					right: 0;
					width: 200px;
					transform: translate(25%, 40%);
					.orn-9 {
						position: absolute;
						bottom: 0;
						right: 0;
						width: 120px;
						transform: translate(-15%, 30%);
					}
				}
				.orn-10 {
					position: absolute;
					top: 0;
					right: 0;
					width: 120px;
					transform: translate(-5%, -10%);
					.orn-11 {
						position: absolute;
						top: 0;
						right: 0;
						width: 100px;
						transform: translate(-15%, -65%);
					}
				}
			}
			.orn-12 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 240px;
				transform: translate(15%, 40%) scaleX(-1);
			}
		}
		.thank {
			position: relative;
			mask-image: url(../images/mask.png);
			mask-size: 100% 100%;
			mask-repeat: no-repeat;
			color: var(--color-light);
			&::before {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: var(--color-primary);
				opacity: 0.7;
				content: "";
			}
			&-body {
				position: relative;
				text-align: center;
				padding: 10rem 2rem;
				@include max-md {
					padding: 8rem 2rem;
				}
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
